* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.main {
  list-style: none;
}

.main__item {
  margin-bottom: 40px;
}

.main__link {
  max-width: 100px;
  text-shadow: 1px 1px #fcf8f8;
  font-size: 14px;
  font: inherit;
  color: #102878;
  cursor: pointer;
  background-color: #9acd32;
  border: 0;
  border-radius: 20px;
  outline: 0;
  margin-bottom: 20px;
  padding: 6px 12px;
  font-weight: normal;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: -5px -5px 20px #fcf8f8, 5px 5px 20px #babecc;
}

.main__link:hover {
  box-shadow: -2px -2px 5px #fcf8f8, 2px 2px 5px #babecc;
}

.main__link:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fcf8f8;
}

.body-img {
  background: url("background.b1cc9808.jpg") no-repeat;
}

.btn-box {
  justify-content: center;
  margin-top: 200px;
  display: flex;
}

.main__link-btn {
  max-width: 100px;
  text-shadow: 1px 1px #fcf8f8;
  font: inherit;
  color: #102878;
  cursor: pointer;
  background-color: #9acd32;
  border: 0;
  border-radius: 20px;
  outline: 0;
  justify-content: center;
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 12px 36px;
  font-weight: normal;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: -5px -5px 20px #fcf8f8, 5px 5px 20px #babecc;
  font-size: 24px !important;
}

.main__link-btn:hover {
  box-shadow: -2px -2px 5px #fcf8f8, 2px 2px 5px #babecc;
}

.main__link-btn:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fcf8f8;
}

